import { supabase } from "@/lib/supabase";

export async function signUp(email, password, userData) {
  try {
    const { data: authData, error: authError } = await supabase.auth.signUp({
      email,
      password,
      options: {
        data: {
          first_name: userData.first_name,
          last_name: userData.last_name,
        },
      },
    });

    if (authError) {
      return { data: null, error: authError };
    }

    if (authData.user) {
      const { data: profileData, error: profileError } = await supabase
        .from("users")
        .insert([
          {
            user_id: authData.user.id,
            first_name: authData.first_name,
            last_name: authData.last_name,
            email: email,
          },
        ])
        .select();

      if (profileError) {
        console.error("Profile creation error: ", profileError);
        await supabase.auth.admin.deleteUser(authData.user.id);
        return { data: null, error: profileError };
      }

      return {
        data: {
          user: authData.user,
          profile: profileData[0],
        },
        error: null,
      };
    }

    return { data: null, error: new Error("User creation failed") };
  } catch (error) {
    console.error("Sign up error:", error);
    return { data: null, error };
  }
}

export async function signIn(email, password) {
  const { data, error } = await supabase.auth.signInWithPassword({
    email,
    password,
  });
  return { data, error };
}

export async function signOut() {
  const { error } = await supabase.auth.signOut();
  return { error };
}

export async function getUserWallets(userId) {
  const { data, error } = (
    await supabase.from("wallets").select("*").eq("user_id", userId)
  ).order("created_at", { ascending: false });

  return { data, error };
}

export async function createWallet(walletData) {
  const { data, error } = await supabase
    .from("wallets")
    .insert([walletData])
    .select();

  return { data, error };
}

export async function getTransactions(userId, filters = {}) {
  let query = supabase
    .from("transactions")
    .select(`*, categories (*)`)
    .eq("user_id", userId);

  if (filters.wallet_id) {
    query = query.eq("wallet_id", filters.wallet_id);
  }

  if (filters.startDate && filters.endDate) {
    query = query.gte("date", filters.startDate).lte("date", filters.endDate);
  }

  const { data, error } = await query.order("date", { ascending: false });
  return { data, error };
}

export async function createTransaction(transactionData) {
  const { data, error } = await supabase
    .from("transactions")
    .insert([transactionData])
    .select();

  return { data, error };
}

